    transform: translate3d(0);
  }
}

/*****************************************/
/* FX Slide animation */
/*****************************************/
.fxSlide .owl-stage {
  perspective: 1200px;
}

.fxSlide .owl-item:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  pointer-events: none;
  -webkit-transition: opacity 0.5s ease-in-out;
  -moz-transition: opacity 0.5s ease-in-out;
  transition: opacity 0.5s ease-in-out;
}

.fxSlide .owl-item.fxSlideOutNext:after, .fxSlide .owl-item.fxSlideOutPrev:after {
  opacity: 1;
}

.fxSlide .owl-item.fxSlideInNext, .fxSlide .owl-item.fxSlideInPrev {
  z-index: 30;
  opacity: 1;
}

.fxSlideOutNext {
  -webkit-transform-origin: 0% 50%;
  -moz-transform-origin: 0% 50%;
  transform-origin: 0% 50%;
  -webkit-animation: rotateOutRight 0.1s forwards ease-in-out;
  -moz-animation: rotateOutRight 0.1s forwards ease-in-out;
  animation: rotateOutRight 0.1s forwards ease-in-out;
}

.fxSlideInNext {
  -webkit-animation: slideFromRightFast 0.3s forwards ease;
  -moz-animation: slideFromRightFast 0.3s forwards ease;
  animation: slideFromRightFast 0.3s forwards ease;
}

.fxSlideOutPrev {
  -webkit-transform-origin: 100% 0%;
  -moz-transform-origin: 100% 0%;
  transform-origin: 100% 0%;
  -webkit-animation: rotateOutLeft 0.1s forwards ease-in-out;
  -moz-animation: rotateOutLeft 0.1s forwards ease-in-out;
  animation: rotateOutLeft 0.1s forwards ease-in-out;
}

.fxSlideInPrev {
  -webkit-animation: slideFromLeftFast 0.3s forwards ease;
  -moz-animation: slideFromLeftFast 0.3s forwards ease;
  animation: slideFromLeftFast 0.3s forwards ease;
}

